* {
	margin: 0;
	font-size: inherit;
	border: 0;
	background-color: unset;
	color: #ffffff;
	border-radius: 0.5rem;
}
body {
	font-size: 1rem;
	font-family: 'Roboto';
	background-color: #121212;
}
.app {
	background-color: rgba(255, 255, 255, 0.05);
	padding: 0.5rem;
	margin: 0.5rem;
}
textarea {
	display: block;
	resize: none;
	width: 100%;
	background-color: rgba(255, 255, 255, 0.02);
	height: 15em;
}
.menus {
	overflow: hidden;
	text-align: center;
	background-color: rgba(255, 255, 255, 0.02);
	padding: 0.5rem;
	margin-bottom: 0.5rem;
	margin-top: 0.5rem;
	margin-left: 0;
	user-select: none;
}
select {
	-moz-appearance: none;
	-webkit-appearance: none;
	background-color: rgba(255, 255, 255, 0.01);
	padding: 0.5rem;
	width: 100%;
	text-align: center;
}

#find {
	margin-bottom: 0.5em;
}
#replace {
	margin-top: 0.5em;
}

option {
	background-color: #121212;
}

option:disabled {
  color:rgba(255, 255, 255, 0.5);
} 

select::-ms-expand {
  display: none;
}